import React from 'react';
import format from 'date-fns/format';
import styled from 'styled-components';
import Img from 'gatsby-image';
import Link from 'gatsby-link';
import Overdrive from 'react-overdrive';

import config from '../../config/SiteConfig';
import * as palette from '../../config/Style';

const CardItem = styled.div`
  min-height: 500px;
  position: relative;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  display: flex;
  transition: all 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);

  &:after {
    content: '';
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    left: 0;
    right: 0;
    bottom: 0;
    transition: all 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    opacity: 1;
  }
`;

const Cover = styled.div`
  width: 100%;
  height: 100%;
  position: absolute;
  div {
    overflow: hidden;
  }
`;

const DateWrapper = styled.div`
  font-size: 0.9rem;
  color: #333;
`;

const Data = styled.div`
  z-index: 10;
  position: relative;
  width: 100%;
  background: white;
  margin-top: -6rem;
  margin-left: 2rem;
  margin-right: -2rem;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.2);
`;

const Content = styled.div`
  padding: 1rem;
  position: relative;
  transition: all 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  opacity: 1;
`;

const Areas = styled.span`
  font-size: 0.75rem;
  color: #333;
`;

const AreaItem = styled.span`
  &:not(:last-child) {
    margin-right: 0.25rem;
    &:after {
      content: ',';
    }
  }
`;

const Name = styled.h2`
  margin-top: 1.25rem;
  margin-bottom: 0;
  color: #333;
`;

const Card = ({ path, cover, date, areas, title, slug }) => (
  <div>
    <Overdrive id={`${slug}-cover`}>
      <CardItem>
        <Cover>
          <Img sizes={cover} />
        </Cover>
      </CardItem>
    </Overdrive>
    <Data>
      <Link to={path}>
        <Content>
          <Name>{title}</Name>
          <DateWrapper>
            <Date date={date} />
          </DateWrapper>
          <Areas>{areas.map(area => <AreaItem key={area}>{area}</AreaItem>)}</Areas>
        </Content>
      </Link>
    </Data>
  </div>
);

export default Card;

const Date = ({ date }) => {
  const formatted = format(date, config.dateFormat);

  return <span>{formatted}</span>;
};



